import { useState, useEffect } from 'react';
import './TicTacToe.scss';

const TicTacToe = ({ playerName, roomId }) => {
  const [board, setBoard] = useState(Array(9).fill(''));
  const [currentPlayer, setCurrentPlayer] = useState('X');
  const [gameActive, setGameActive] = useState(true);
  const [status, setStatus] = useState('PLAYER X TURN');
  const [opponent, setOpponent] = useState('WAITING');

  const winningConditions = [
    [0, 1, 2], [3, 4, 5], [6, 7, 8], // rows
    [0, 3, 6], [1, 4, 7], [2, 5, 8], // columns
    [0, 4, 8], [2, 4, 6]             // diagonals
  ];

  const handleCellClick = (index) => {
    if (board[index] !== '' || !gameActive) return;
    
    const newBoard = [...board];
    newBoard[index] = currentPlayer;
    setBoard(newBoard);
    
    checkResult(newBoard);
  };

  const checkResult = (currentBoard) => {
    let roundWon = false;
    
    for (let i = 0; i < winningConditions.length; i++) {
      const [a, b, c] = winningConditions[i];
      if (currentBoard[a] && currentBoard[a] === currentBoard[b] && currentBoard[a] === currentBoard[c]) {
        roundWon = true;
        highlightWinningCells([a, b, c]);
        break;
      }
    }
    
    if (roundWon) {
      setStatus(`PLAYER ${currentPlayer} WINS!`);
      setGameActive(false);
      return;
    }
    
    if (!currentBoard.includes('')) {
      setStatus('DRAW!');
      setGameActive(false);
      return;
    }
    
    const nextPlayer = currentPlayer === 'X' ? 'O' : 'X';
    setCurrentPlayer(nextPlayer);
    setStatus(`PLAYER ${nextPlayer} TURN`);
  };

  const highlightWinningCells = (cells) => {
    // Highlighting handled via SCSS
  };

  const resetGame = () => {
    setBoard(Array(9).fill(''));
    setCurrentPlayer('X');
    setGameActive(true);
    setStatus('PLAYER X TURN');
  };

  return (
    <div className="tic-tac-toe">
      <div className="crt-effect"></div>
      <div className="screen-glow"></div>
      
      <div className="game-hud">
        <div className="player-info">
          <p>P1: {playerName}</p>
          <p>ROOM: {roomId}</p>
          <p>P2: {opponent}</p>
        </div>
        
        <div className="game-status">{status}</div>
        
        <div className="game-board">
          {board.map((cell, index) => (
            <div 
              key={index}
              className={`board-cell ${getBorderClasses(index)} ${cell ? 'occupied' : ''}`}
              onClick={() => handleCellClick(index)}
            >
              {cell && <div className="player-symbol">{cell}</div>}
            </div>
          ))}
        </div>
      </div>
      
      <button className="reset-button" onClick={resetGame}>RESTART (R)</button>
    </div>
  );
};

// Helper function for border classes
const getBorderClasses = (index) => {
  const classes = [];
  if ([0, 1, 3, 4, 6, 7].includes(index)) classes.push('b-right');
  if ([1, 2, 4, 5, 7, 8].includes(index)) classes.push('b-left');
  if ([3, 4, 5, 6, 7, 8].includes(index)) classes.push('b-top');
  if ([0, 1, 2, 3, 4, 5].includes(index)) classes.push('b-bottom');
  return classes.join(' ');
};

export default TicTacToe;